@import url(https://fonts.googleapis.com/css2?family=Varela&display=swap);
.btl-header{width:100%;height:5em;padding:2em;background-color:transparent;z-index:2;position:absolute}

@font-face{font-family:'BlackBerry';src:url(/bottle-web/static/media/BlackBerryJam.c6b7ba1d.ttf) format("truetype")}.btl-logo{font-family:'BlackBerry';color:white;padding:2em;font-size:xx-large}

.btl-section{width:100%;position:relative;height:100%}.btl-section .btl-section-background{position:absolute}.btl-section .btl-section-content{position:relative;height:100%;width:100%}

.btl-jumbobtn:link,.btl-jumbobtn:visited{text-transform:uppercase;text-decoration:none;padding:15px 40px;display:inline-block;border-radius:100px;transition:all .2s;position:relative}.btl-jumbobtn:hover{transform:translateY(-3px);box-shadow:0 10px 20px rgba(0,0,0,0.2);color:#5000F1}.btl-jumbobtn:active{transform:translateY(-1px);box-shadow:0 5px 10px rgba(0,0,0,0.2)}.btl-jumbobtn-white{background-color:#fff;color:#777}.btl-jumbobtn::after{content:"";display:inline-block;height:100%;width:100%;border-radius:100px;position:absolute;top:0;left:0;z-index:-1;transition:all .4s}.btl-jumbobtn-white::after{background-color:#fff}.btl-jumbobtn:hover::after{transform:scaleX(1.4) scaleY(1.6);opacity:0}.btl-jumbobtn-animated{-webkit-animation:moveInBottom .5 ease-out .75s;animation:moveInBottom .5 ease-out .75s;-webkit-animation-fill-mode:backwards;animation-fill-mode:backwards}

.btl-video{z-index:1;object-fit:cover}

.btl-jumbotron-description-container{display:flex;width:90%;flex-direction:column;justify-content:center;align-items:flex-start}.btl-jumbotron-description-container .btl-jumbotron-description{font-size:2em;line-height:1.7;color:white;font-weight:600;width:24em}

.btl-mission-description .btl-mission-description-header{font-size:2em;font-weight:900;letter-spacing:0.12em;font-family:'Muli', sans-serif;margin-bottom:0.7em}.btl-mission-description .btl-mission-description-body{font-size:1.5em;line-height:1.7;font-family:'Muli', sans-serif}.btl-mission-description .btl-mission-description-image{width:100%}.btl-MaskBenefits{margin-top:3em;margin-bottom:3em;width:100%}.btl-CardContainer{display:flex;justify-content:center;padding:5em}.btl-CardContainer .btl-Card{margin:0em 2em}.btl-divider{color:#00ebb0;background-color:#00ebb0;height:0.2em;border-radius:1em;width:15em;border:none;margin-top:2em;margin-bottom:2em}.btl-BadgeContainer{display:flex;justify-content:center;margin:0em 10em;padding:5em}.btl-BadgeContainer img{width:10em;margin:0em 8em}.btl-MaskContainer{margin-top:1.5em;display:flex;width:100%;justify-content:space-between}

.btl-Card{perspective:1000px;border-radius:1em}.btl-Card .btl-CardShine{position:absolute;top:0;left:0;right:0;bottom:0}.btl-Card .btl-CardContent{display:flex;flex-direction:column;position:absolute;padding:1em;left:0;bottom:0em;margin:0;background-image:linear-gradient(to top, black, rgba(0,0,0,0));height:auto;width:100%}.btl-Card .btl-CardContent .btl-CardTitle{font-size:2em;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";color:white;transition:all 0.1s ease-in-out}.btl-Card .btl-CardContent .btl-CardDescription{font-size:1em;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";color:white;-webkit-animation-name:splat-to-left;animation-name:splat-to-left;-webkit-animation-duration:1000ms;animation-duration:1000ms}.btl-Card .btl-CardImage{width:20em;height:30em;object-fit:cover}

.btl-Team-Container{max-width:1170px;width:100%;margin:100px auto;padding:0 20px;box-sizing:border-box;transform-origin:top center;transform:scale(0.8)}.btl-Team-Container .btl-Team-Title{margin:0 0 70px;color:#000;font-family:'Roboto', sans-serif;font-size:3.5rem;font-weight:300;line-height:2.625rem;text-align:center}.btl-Team-Container .btl-Team-Grid{display:grid;grid-template-columns:auto auto auto;background:#FFF;border:1px solid #E2E9ED}.btl-Team-Container .btl-Team-UserCard{width:100%;text-align:center;z-index:0;position:relative;background:#FFF;box-shadow:0 0 0 1px #E2E9ED;padding:5em 0em;box-sizing:border-box;transition:box-shadow 0.2s ease, transform 0.2s ease, z-index 0s 0.2s ease}.btl-Team-Container .btl-Team-UserCard:before{content:"";display:block}.btl-Team-Container .btl-Team-UserCard:hover{z-index:1;box-shadow:0 8px 50px rgba(0,0,0,0.2);transform:scale(1.05);transition:box-shadow 0.2s ease, transform 0.2s ease, z-index 0s 0s ease}.btl-Team-Container .btl-Team-UserCard .btl-Team-UserCard-Avatar{width:98px;height:98px;border-radius:100%;margin:0 auto 20px;overflow:hidden}.btl-Team-Container .btl-Team-UserCard .btl-Team-UserCard-Avatar img{display:block;width:100%;max-width:100%}.btl-Team-Container .btl-Team-UserCard .btl-Team-UserCard-Name{color:#313435;font-family:'Roboto', sans-serif;font-size:1.5rem;font-weight:500;line-height:2.625rem}.btl-Team-Container .btl-Team-UserCard .btl-Team-UserCard-Title{color:#6F808A;font-family:'Roboto', sans-serif;font-size:0.875rem;line-height:2.375rem}.btl-Team-Container .btl-Team-UserCard .btl-Team-UserCard-Social{margin:0 0 -18px;opacity:0;transition:margin 0.2s ease, opacity 0.2s ease}

.btl-zoom-image-card{overflow:hidden;position:relative;border:none;height:22em;width:22em}.btl-zoom-image-card img{transition:transform .25s;width:100%}.btl-zoom-image-card:hover img{transform:scale(1.2);cursor:pointer}.btl-zoom-image-card:hover{box-shadow:0 1px 1px rgba(0,0,0,0.11),0 2px 2px rgba(0,0,0,0.11),0 4px 4px rgba(0,0,0,0.11),0 8px 8px rgba(0,0,0,0.11),0 16px 16px rgba(0,0,0,0.11),0 32px 32px rgba(0,0,0,0.11)}

.btl-productcard-container{font-family:'Roboto', sans-serif;min-width:290px;width:290px;height:500px;margin:30px 38px;cursor:pointer;perspective:800px;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform-style:preserve-3d;transition:transform 1s;transform-origin:right center}.btl-productcard-container .btl-productcard-flipped{transform:rotateY(-180deg)}.btl-productcard-container .btl-productcard-front{transform:translateX(-100%) rotateY(-180deg)}.btl-productcard-container .btl-productcard-back{transform:rotateY(180deg);padding:1em}.btl-productcard-container .btl-productcard-card{transition:transform 330ms ease-in-out;border-radius:25px;box-shadow:0 1px 1px 1px rgba(0,0,0,0.11),0 2px 2px 2px rgba(0,0,0,0.11),0 4px 4px 4px rgba(0,0,0,0.11),0 8px 8px 8px rgba(0,0,0,0.11),0 16px 16px 16px rgba(0,0,0,0.11),0 32px 32px 32px rgba(0,0,0,0.11)}.btl-productcard-container .btl-productcard-card:hover:not(.btl-productcard-flipped){box-shadow:0 2px 2px 2px rgba(0,0,0,0.11),0 4px 4px 4px rgba(0,0,0,0.11),0 8px 8px 8px rgba(0,0,0,0.11),0 16px 16px 16px rgba(0,0,0,0.11),0 32px 32px 32px rgba(0,0,0,0.11),0 40px 40px 40px rgba(0,0,0,0.11);transform:translateY(-2px)}.btl-productcard-container .btl-productcard-card:hover .btl-productcard-product{transform:translateY(-6em)}.btl-productcard-container .btl-productcard-card-head{position:relative;height:300px;border-radius:25px 25px 0 0}.btl-productcard-container .btl-productcard-card-head .btl-productcard-logo{width:15em;margin-left:-4em;margin-top:-1em}.btl-productcard-container .btl-productcard-card-head .btl-productcard-product{transition:transform 630ms ease-in-out;position:absolute;overflow:visible !important;left:0;width:20em;margin-top:-1em;margin-left:7em}.btl-productcard-container .btl-productcard-card-head .btl-productcard-details{width:100%;overflow-x:hidden;padding:0 20px;font-size:11px;color:#fff}.btl-productcard-container .btl-productcard-card-head .btl-productcard-back-text{width:100%;padding:0px 10px;display:inline-block;font-size:125px;font-weight:900;margin-top:-5em;max-width:240px;overflow-x:wrap;opacity:0.1}.btl-productcard-container .btl-productcard-card-body{height:19em;background:#fff;border-radius:0 0 25px 25px}.btl-productcard-container .btl-productcard-card-body .btl-productcard-product-title{padding:20px 20px 5px 20px;display:block;font-size:17px;font-weight:500;letter-spacing:1px;text-transform:uppercase}.btl-productcard-container .btl-productcard-card-body .btl-productcard-product-title b{font-weight:900;letter-spacing:0px}.btl-productcard-container .btl-productcard-card-body .btl-productcard-product-caption{display:block;padding:0 20px;font-size:10px;font-weight:400;text-transform:uppercase}.btl-productcard-container .btl-productcard-card-body .btl-productcard-product-price{position:absolute;background:#11e95b;padding:7px 20px;text-align:center;display:inline-block;font-size:24px;font-weight:200;color:#fff;border-radius:7px;margin-top:-13px;margin-left:8.5em;box-shadow:-10px 20px 15px -10px rgba(17,233,91,0.3)}.btl-productcard-container .btl-productcard-card-body .btl-productcard-product-price b{margin-left:5px}.btl-productcard-container .btl-productcard-card-body .btl-productcard-product-properties{padding:0 20px;font-size:12px}.btl-productcard-container .btl-productcard-card-body .btl-productcard-product-properties li{font-size:inherit}.btl-productcard-container .btl-productcard-badge{position:relative;font-size:10px;font-weight:300;color:#fff;background:#11e95b;padding:2px 5px;border-radius:4px;top:-2px;margin-left:5px}

