.btl-buy-container{width:100%;display:flex;padding:0.5em;justify-content:space-between}.btl-buy-price{font-size:1.5em;align-self:center;color:#1baa4c}.btl-buy{border:none;text-decoration:none;background:#11e95b;padding:0.35em;color:white;font-size:1.5em;font-weight:200;border-radius:7px;box-shadow:0px 20px 15px -10px rgba(17,233,91,0.3);transition:all .2s;cursor:pointer}.btl-buy:hover{transform:translateY(-3px);box-shadow:0px 23px 15px -10px rgba(17,233,91,0.3)}

