.btl-Footer-Container{display:flex;flex-direction:column;justify-content:space-between;height:100%;width:100%;padding:2em;color:#a2aed2}.btl-Footer-Container .btl-Footer-Row1{display:flex;justify-content:flex-start;margin-top:1em}.btl-Footer-Container .btl-Footer-Row1 span{padding:0em;margin-left:2em;color:#a2aed2}.btl-Footer-Container .btl-Footer-Row2{display:flex;justify-content:space-between;flex-wrap:wrap;margin-top:3em;padding:0em 15em}.btl-Footer-Container .btl-Footer-Row2-Left{display:flex;justify-content:space-between;flex-wrap:wrap}.btl-Footer-Container .btl-Footer-Summary{font-family:'Varela', sans-serif;width:25em}.btl-Footer-Container .btl-Footer-Summary .btl-Footer-Summary-Title{text-transform:uppercase;font-size:1.2em;font-weight:bold}.btl-Footer-Container .btl-Footer-Summary .btl-Footer-Summary-Description{margin-top:1.5em}.btl-Footer-Container .btl-Footer-Summary .btl-Footer-Summary-Items{display:flex;flex-direction:column;margin-top:1.5em}.btl-Footer-Container .btl-Footer-Summary .btl-Footer-Summary-Items a{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;color:#a2aed2;cursor:pointer}.btl-Footer-Container .btl-Footer-Summary .btl-Footer-Summary-Items a:hover{color:#a2aed2}.btl-Footer-Container .btl-Footer-Summary .btl-Footer-Summary-Item{margin-top:0.5em}.btl-Footer-Container .btl-Footer-Summary .btl-Footer-Summary-SocialContainer{display:flex;width:100%;margin-top:1.5em}.btl-Footer-Container .btl-Footer-Summary .btl-Footer-Summary-SocialContainer svg{margin-right:3.5em}.btl-Footer-Container .btl-Footer-Tail{display:flex;justify-content:space-between;border-top:1px solid #a2aed2;padding:2em 0em 0em 2em}.btl-Footer-Container .btl-Footer-Tail .btl-Footer-TailLinks{width:70%;display:flex;justify-content:space-evenly}.btl-connect-button{cursor:pointer}

html{scroll-behavior:smooth}.btl-App{background-color:#282c34;background-color:white}.btl-NaviagtionLink-Container{float:right;font-size:1.5em}.btl-NaviagtionLink-Container a{margin-left:1.5em}.btl-Divider{width:50%}

.btl-NavigationLink{color:white}

.btl-parallax-bottom{display:flex;justify-content:center;width:100%;padding-top:50px;padding-bottom:100px;z-index:0;position:relative;color:white;background-color:#343f60}.btl-parallax-top{padding-top:50px;padding-bottom:50px;display:flex;justify-content:center;z-index:1;position:relative;width:100vw;background-color:white;box-shadow:0 1px 1px 1px rgba(0,0,0,0.11),0 2px 2px 2px rgba(0,0,0,0.11),0 4px 4px 4px rgba(0,0,0,0.11),0 8px 8px 8px rgba(0,0,0,0.11),0 16px 16px 16px rgba(0,0,0,0.11),0 32px 32px 32px rgba(0,0,0,0.11)}.btl-section-content{width:60%}

